@use "../util/" as *;

.home-page {
  .cursor {
    display: none;
    pointer-events: none;
    @include breakpoint(large) {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      position: fixed;
      top: 0;
      left: 0;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      z-index: 100;
      background-color: var(--warna-biru);
    }
    &.foto {
      background-color: var(--warna-cream);
    }
  }

  background-color: var(--warna-cream);
  text-transform: uppercase;

  .home-page-container {
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    flex-direction: column;
    @include breakpoint(large) {
      flex-direction: row;
    }
    .fotografer-section {
      .fotografi-container {
        overflow: hidden;
      }
      font-size: 3vw;
      color: var(--warna-cream);
      background-color: var(--warna-biru);
      display: flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      @include breakpoint(large) {
        font-size: 1.5vw;
      }
    }
    .fullstack-section {
      .fullstack-container {
        overflow: hidden;
      }
      font-size: 3vw;
      color: var(--warna-biru);
      background-color: var(--warna-cream);
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint(large) {
        font-size: 1.5vw;
      }
    }
  }
}

.home-page-transition {
  z-index: -1;
  top: 0;
  position: absolute;

  .cursor {
    display: none;
    pointer-events: none;
    @include breakpoint(large) {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      position: fixed;

      top: 0;
      left: 0;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;

      z-index: 100;
      background-color: var(--warna-biru);
    }
    &.foto {
      background-color: var(--warna-cream);
    }
  }
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  background-color: var(--warna-biru);
  text-transform: uppercase;
  .nav-mobile {
    @include breakpoint(large) {
      display: none;
    }
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: flex-end;
    padding: 2rem 2rem;
    .ham {
      font-size: 0;
      color: var(--warna-cream);
    }
  }
  .nav-home-page {
    @include breakpoint-max(large) {
      display: none;
    }
    overflow: hidden;
    font-size: 1vw;
    width: 100vw;
    position: absolute;
    // margin: 0 3rem 3rem 3rem;

    color: var(--warna-cream);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .nav-kiri {
      padding: 3rem 0 3rem 3rem;
      margin-right: 1rem;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      #about {
        border-radius: 40px;
        padding: 0.5rem 2rem;
      }
    }
    .nav-kanan {
      padding: 3rem 3rem 3rem 0;
      margin-left: 1rem;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: var(--warna-biru);
      #contact {
        border-radius: 40px;
        padding: 0.5rem 2rem;
      }
    }
  }
  .home-page-container {
    h1 {
      overflow: hidden;
    }

    h2 {
      overflow: hidden;
    }
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    flex-direction: column;
    @include breakpoint(large) {
      flex-direction: row;
    }
    .fotografer-section {
      font-size: 3vw;
      white-space: nowrap;

      color: var(--warna-cream);
      display: flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      @include breakpoint(large) {
        font-size: 1.5vw;
      }
    }
    .fullstack-section {
      font-size: 3vw;

      color: var(--warna-biru);
      background-color: var(--warna-cream);
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint(large) {
        font-size: 1.5vw;
      }
    }
  }
}
