.home-page {
  background-color: var(--warna-cream);
  text-transform: uppercase;
}
.home-page .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .home-page .cursor {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    background-color: var(--warna-biru);
  }
}
.home-page .cursor.foto {
  background-color: var(--warna-cream);
}
.home-page .home-page-container {
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 64rem) {
  .home-page .home-page-container {
    flex-direction: row;
  }
}
.home-page .home-page-container .fotografer-section {
  font-size: 3vw;
  color: var(--warna-cream);
  background-color: var(--warna-biru);
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.home-page .home-page-container .fotografer-section .fotografi-container {
  overflow: hidden;
}
@media screen and (min-width: 64rem) {
  .home-page .home-page-container .fotografer-section {
    font-size: 1.5vw;
  }
}
.home-page .home-page-container .fullstack-section {
  font-size: 3vw;
  color: var(--warna-biru);
  background-color: var(--warna-cream);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-page .home-page-container .fullstack-section .fullstack-container {
  overflow: hidden;
}
@media screen and (min-width: 64rem) {
  .home-page .home-page-container .fullstack-section {
    font-size: 1.5vw;
  }
}

.home-page-transition {
  z-index: -1;
  top: 0;
  position: absolute;
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  background-color: var(--warna-biru);
  text-transform: uppercase;
}
.home-page-transition .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .home-page-transition .cursor {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    background-color: var(--warna-biru);
  }
}
.home-page-transition .cursor.foto {
  background-color: var(--warna-cream);
}
.home-page-transition .nav-mobile {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  padding: 2rem 2rem;
}
@media screen and (min-width: 64rem) {
  .home-page-transition .nav-mobile {
    display: none;
  }
}
.home-page-transition .nav-mobile .ham {
  font-size: 0;
  color: var(--warna-cream);
}
.home-page-transition .nav-home-page {
  overflow: hidden;
  font-size: 1vw;
  width: 100vw;
  position: absolute;
  color: var(--warna-cream);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 64rem) {
  .home-page-transition .nav-home-page {
    display: none;
  }
}
.home-page-transition .nav-home-page .nav-kiri {
  padding: 3rem 0 3rem 3rem;
  margin-right: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home-page-transition .nav-home-page .nav-kiri #about {
  border-radius: 40px;
  padding: 0.5rem 2rem;
}
.home-page-transition .nav-home-page .nav-kanan {
  padding: 3rem 3rem 3rem 0;
  margin-left: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--warna-biru);
}
.home-page-transition .nav-home-page .nav-kanan #contact {
  border-radius: 40px;
  padding: 0.5rem 2rem;
}
.home-page-transition .home-page-container {
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
  flex-direction: column;
}
.home-page-transition .home-page-container h1 {
  overflow: hidden;
}
.home-page-transition .home-page-container h2 {
  overflow: hidden;
}
@media screen and (min-width: 64rem) {
  .home-page-transition .home-page-container {
    flex-direction: row;
  }
}
.home-page-transition .home-page-container .fotografer-section {
  font-size: 3vw;
  white-space: nowrap;
  color: var(--warna-cream);
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
@media screen and (min-width: 64rem) {
  .home-page-transition .home-page-container .fotografer-section {
    font-size: 1.5vw;
  }
}
.home-page-transition .home-page-container .fullstack-section {
  font-size: 3vw;
  color: var(--warna-biru);
  background-color: var(--warna-cream);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 64rem) {
  .home-page-transition .home-page-container .fullstack-section {
    font-size: 1.5vw;
  }
}